import React, { Component } from 'react';
import { CollapseButton } from './Utils.js';
import withCollapse from './withCollapse.js';

const TXC = props => (
  <div className="card text-white mb-3">
    <div className="card-header">
      <h3 className="text-monospace">
        Buy TXS
        <CollapseButton
          onCollapse={props.onCollapse}
          collapsed={props.collapsed}
        />
      </h3> 
    </div>  
    <div className={"card-body" + (props.collapsed ? " card-collapse" : "")}>
      <p className="lead">TXS is the subcription badge of TXCast. In order to be able to place a scheduled TX request, your address needs 1 TXS per request stored at the server. Once the request is cleared, your TXS will still be yours and you'll be ready to place another request.</p>
      <p>There will only be a total of 10000 TXCast subscriptions. Each sub goes for 0.2 ETH. Subscriptions aren't subdivisible. However, an address can buy as many whole units of TXS as it wishes. Subscriptions do not expire and can be transferred between addresses, so you can sell your TXS or gift it to a friend..</p>
      <div className="form-group">
        <button
          type="button"
          onClick={() => props.onBuy()}
          className="btn btn-secondary btn-lg btn-block"
        >
          Buy TXS
        </button>
      </div>
    </div>
  </div>
);

class TXCContainer extends Component {
  render() {
    return(
      <TXC
        {...this.props}
      />
    );  
  }
}

export default withCollapse(TXCContainer);
